@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Tajawal:wght@200;300;400;500;700;800&display=swap');
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
    
}
body {
  font-family: IRANSansnum;
  font-style: normal;
  font-weight: 500;
  visibility: visible;
  direction: rtl;
  color: #222222;
  background-color: rgb(241, 237, 233);
  
}
span {
  font-size: 14px;
  color: #222222;
}
a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
  transition: all 0.3s ease;
}
a:hover {
  color: #222222;
}
